/* Acordeon styles */
.accordion-wrap {
  .truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .tab {
    position: relative;
    margin-bottom: 1px;
    width: 100%;
    color: #fff;
    overflow: hidden;
  }
  //.button-container {
  //  text-align: center;
  //}
  input {
    position: absolute;
    opacity: 0;
    z-index: -1;
  }
  label {
    position: relative;
    display: block;
    padding: 0 0 0 1em;
    background: #5c476b;
    font-weight: bold;
    line-height: 16px;
    cursor: pointer;
    padding: 12px 30px 12px 13px;
    font-size: 12px;
  }
  .blue label {
    background: #2980b9;
  }
  .tab-content {
    max-height: 0;
    overflow: hidden;
    background: #eaeaea;
    -webkit-transition: max-height .35s;
    -o-transition: max-height .35s;
    transition: max-height .35s;

    button {
      background: none;
      cursor: pointer;
      -webkit-appearance: none;
      border: 0;
      padding: 10px 25px;
      color: #5c476b;
      width: 100%;
      -webkit-transition: background .35s;
      -o-transition: background .35s;
      transition: background .35s;
      outline: none;
      text-align: left;
      span {
        padding-left: 5px;
      }
      &:hover {
        background: #cecece;
      }
    }
  }
  .blue .tab-content {
    background: #3498db;
  }
  .tab-content p {
    margin: 1em;
  }
  /* :checked */
  input:checked ~ .tab-content {
    max-height: 20em;
  }
  /* Icon */
  label::after {
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    width: 3em;
    height: 3em;
    line-height: 3;
    text-align: center;
    -webkit-transition: all .35s;
    -o-transition: all .35s;
    transition: all .35s;
  }
  input[type=checkbox] + label::after {
    content: "+";
  }
  input[type=radio] + label::after {
    content: "\25BC";
  }
  input[type=checkbox]:checked + label::after {
    transform: rotate(315deg);
  }
  input[type=radio]:checked + label::after {
    transform: rotateX(180deg);
  }

}



